import Image from 'next/image'
import Link from 'next/link'

export default function ProjectsContent() {
  const projects = [
    {
      name: 'Construction Projects Management',
      image: '/images/proj_construction.jpg',
      description: 'Comprehensive web application for construction project management, featuring real-time collaboration, secure billing systems, and advanced document management.',
      tech: ['Node.js', 'Express.js', 'MongoDB', 'Socket.IO', 'Cloudinary'],
      live: 'https://construction-projects-mgmt.onrender.com',
      github: 'https://github.com/sohelkureshi/Constructionprojectsmgmt',
      category: 'Full-Stack Application',
      icon: 'bi-building'
    },
    {
      name: 'EstateEase',
      image: '/images/estateease_main.png',
      description: 'Modern real estate platform with advanced search capabilities, secure authentication, and property management features.',
      tech: ['React', 'Redux', 'Firebase'],
      live: 'https://estateease-tvuo.onrender.com',
      github: 'https://github.com/sohelkureshi/EstateEase',
      category: 'Real Estate',
      icon: 'bi-house-door'
    },
    {
      name: 'CollabNotes',
      image: '/images/collabnotes.png',
      description: 'A real-time collaborative notetaking app where people can create, organize, share, and coedit notes with version history, search, and access controls.',
      tech: ['Next.js 14', 'Express', 'MongoDB', 'Socket.IO', 'HeroUI', 'Tailwind'],
      live: 'https://collaborative-note-app-flame.vercel.app',
      github: 'https://github.com/sohelkureshi/collaborative-note-app',
      category: 'Productivity',
      icon: 'bi-people'
    },
    {
      name: 'ASIN AI Optimizer',
      image: '/images/AsinAI.png',
      description: 'AI app that fetches Amazon product data by ASIN and generates improved title, five bullets, enriched description, and keyword ideas using Gemini, with MySQL-backed optimization history per ASIN.',
      tech: ['React 18', 'Node.js', 'MySQL', 'Gemini', 'Puppeteer'],
      live: 'https://asin-ai-optimizer.netlify.app',
      github: 'https://github.com/sohelkureshi/ASIN-AI-Optimizer',
      category: 'Ecommerce',
      icon: 'bi-bag'
    },
    {
      name: 'E-Notebook',
      image: '/images/enotebook.png',
      description: 'Secure note-taking application with JWT authentication, real-time sync, and intuitive note management system.',
      tech: ['MERN', 'JWT', 'Bootstrap'],
      live: 'https://e-notebook-fu9z.onrender.com',
      github: 'https://github.com/sohelkureshi/eNotebook',
      category: 'Productivity',
      icon: 'bi-journal-text'
    },
    {
      name: 'Materials Comparison Dashboard',
      image: '/images/proj_material.png',
      description: 'Interactive data visualization platform for comparing material properties with dynamic charts and comprehensive analysis.',
      tech: ['Chart.js', 'Bootstrap', 'JavaScript'],
      live: 'https://sohelkureshi.github.io/Materials-Comparison-Dashboard',
      github: 'https://github.com/sohelkureshi/Materials-Comparison-Dashboard',
      category: 'Analytics',
      icon: 'bi-bar-chart'
    }
  ]

  return (
    <section id="projects" className="py-20 max-w-7xl mx-auto px-4">
      <div className="section-header text-center mb-24">
        <h2 className="text-5xl md:text-6xl font-bold bg-gradient-to-r from-white via-slate-100 to-slate-200 bg-clip-text text-transparent mb-6">
          Featured Projects
        </h2>
        <p className="section-subtitle text-xl text-slate-400 max-w-2xl mx-auto leading-relaxed">
          Transforming ideas into digital reality
        </p>
      </div>

      {/* Featured Project - Construction - EXACT */}
      <div className="projects-showcase mb-24">
        <div className="featured-project relative rounded-3xl overflow-hidden shadow-2xl group cursor-pointer mb-8 lg:mb-0 max-w-4xl mx-auto" tabIndex={0}>
          <div className="project-image-container relative">
            <Image
              src="/images/proj_construction.jpg"
              alt="Construction Projects Management"
              width={1200}
              height={600}
              className="w-full h-[500px] lg:h-[600px] object-cover transition-transform duration-700 group-hover:scale-110"
            />
            <div className="image-overlay absolute inset-0 bg-gradient-to-t from-black/90 via-black/50 to-transparent opacity-0 group-hover:opacity-100 transition-all duration-500 flex items-center justify-center">
              <div className="overlay-content text-center text-white">
                <i className="bi bi-building text-6xl mb-4 opacity-90"></i>
                <span className="text-2xl font-bold bg-white/10 px-6 py-3 rounded-full backdrop-blur-sm">Full-Stack Application</span>
              </div>
            </div>
          </div>
          <div className="project-content relative p-12 lg:p-16 bg-gradient-to-b from-slate-900/95 to-slate-800/70 border-t-2 border-slate-700">
            <div className="project-header flex flex-col lg:flex-row lg:items-center lg:justify-between mb-6">
              <h3 className="text-4xl lg:text-5xl font-bold text-white mb-4 lg:mb-0">Construction Projects Management</h3>
              <div className="project-badge featured inline-block bg-gradient-to-r from-emerald-500 to-green-600 px-8 py-4 rounded-2xl font-bold text-2xl text-white shadow-2xl">
                Featured
              </div>
            </div>
            <p className="project-description text-xl text-slate-300 mb-10 leading-relaxed max-w-3xl">
              Comprehensive web application for construction project management, featuring real-time collaboration, 
              secure billing systems, and advanced document management.
            </p>
            <div className="tech-stack flex flex-wrap gap-3 mb-12">
              {['Node.js', 'Express.js', 'MongoDB', 'Socket.IO', 'Cloudinary'].map((tech) => (
                <span key={tech} className="tech-tag px-5 py-3 bg-slate-800/50 border border-slate-700 rounded-xl font-semibold text-slate-300 hover:bg-slate-700 hover:border-slate-600 transition-all flex items-center">
                  <i className="bi bi-code-slash mr-2"></i>{tech}
                </span>
              ))}
            </div>
            <div className="project-actions flex flex-col sm:flex-row gap-4">
              <Link href="https://construction-projects-mgmt.onrender.com" target="_blank" rel="noopener noreferrer" className="btn-primary group flex-1 flex items-center justify-center gap-3 py-6 px-8 bg-gradient-to-r from-emerald-600 to-green-700 hover:from-emerald-700 hover:to-green-800 text-xl font-bold rounded-2xl border-2 border-transparent hover:border-emerald-500 shadow-2xl hover:shadow-3xl hover:-translate-y-2 transition-all duration-300">
                <i className="bi bi-eye text-xl group-hover:translate-x-1 transition-transform"></i>
                Live Demo
              </Link>
              <Link href="https://github.com/sohelkureshi/Constructionprojectsmgmt" target="_blank" rel="noopener noreferrer" className="btn-secondary flex-1 flex items-center justify-center gap-3 py-6 px-8 bg-gradient-to-r from-slate-800 to-slate-700 hover:from-slate-700 hover:to-slate-600 text-xl font-bold rounded-2xl border-2 border-slate-600 hover:border-slate-500 shadow-xl hover:shadow-2xl hover:-translate-y-1 transition-all duration-300">
                <i className="bi bi-github text-xl group-hover:translate-x-1 transition-transform"></i>
                Source Code
              </Link>
            </div>
          </div>
        </div>
      </div>

      {/* Projects Grid - EXACT 6 projects */}
      <div className="projects-grid grid grid-cols-1 md:grid-cols-2 lg:grid-cols-2 xl:grid-cols-3 gap-8">
        {projects.map((project, index) => (
          <div key={project.name} className="project-card group cursor-pointer rounded-3xl overflow-hidden shadow-2xl bg-gradient-to-br from-slate-900/80 to-slate-800/50 border border-slate-800 hover:border-blue-500/50 hover:shadow-3xl hover:-translate-y-4 transition-all duration-500 hover:bg-slate-900/90 backdrop-blur-xl" tabIndex={0}>
            <div className="card-header relative h-64 overflow-hidden">
              <div className="project-image-wrapper relative h-full w-full">
                <Image
                  src={project.image}
                  alt={project.name}
                  fill
                  className="object-cover transition-transform duration-700 group-hover:scale-110"
                />
                <div className="image-gradient absolute inset-0 bg-gradient-to-t from-black/80 via-transparent to-transparent"></div>
              </div>
              <div className="project-category absolute top-6 left-6 bg-white/10 backdrop-blur-sm border border-white/20 px-5 py-2 rounded-full text-white font-bold flex items-center">
                <i className={`bi ${project.icon} mr-2`}></i>
                {project.category}
              </div>
            </div>
            <div className="card-content p-8 pb-12 relative">
              <h4 className="text-2xl font-bold text-white mb-4 group-hover:text-blue-400 transition-colors">{project.name}</h4>
              <p className="text-slate-400 mb-6 leading-relaxed">{project.description}</p>
              <div className="mini-tech-stack flex flex-wrap gap-2 mb-8">
                {project.tech.map((tech) => (
                  <span key={tech} className="mini-tech px-3 py-1 bg-slate-800/50 border border-slate-700 rounded-lg text-sm font-medium text-slate-300">
                    {tech}
                  </span>
                ))}
              </div>
              <div className="card-footer absolute bottom-6 left-8 right-8 flex gap-4 opacity-0 group-hover:opacity-100 transition-all duration-300">
                {project.live && (
                  <Link href={project.live} target="_blank" rel="noopener noreferrer" className="flex-1 text-center px-6 py-3 bg-blue-600 hover:bg-blue-700 text-white font-bold rounded-xl border border-blue-500/50 transition-all shadow-lg hover:shadow-xl hover:-translate-y-1">
                    Live Demo
                  </Link>
                )}
                <Link href={project.github} target="_blank" rel="noopener noreferrer" className="flex-1 text-center px-6 py-3 bg-slate-800 hover:bg-slate-700 text-slate-300 font-bold rounded-xl border border-slate-600 transition-all shadow-lg hover:shadow-xl hover:-translate-y-1">
                  GitHub
                </Link>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* EXACT cta-section */}
      <div className="cta-section mt-32 pt-24 border-t-4 border-slate-800">
        <div className="cta-content text-center max-w-4xl mx-auto">
          <h3 className="text-4xl lg:text-5xl font-bold text-white mb-8">Explore More Projects</h3>
          <p className="text-xl text-slate-400 mb-12 leading-relaxed max-w-2xl mx-auto">
            Discover additional projects and contributions on my GitHub profile
          </p>
          <Link 
            href="https://github.com/sohelkureshi" 
            target="_blank" 
            rel="noopener noreferrer"
            className="github-btn inline-flex items-center gap-4 px-12 py-6 bg-gradient-to-r from-slate-800 to-slate-900 hover:from-slate-700 hover:to-slate-800 text-2xl font-bold rounded-3xl border-2 border-slate-700 hover:border-slate-600 shadow-2xl hover:shadow-3xl hover:-translate-y-2 transition-all duration-300 group"
          >
            <i className="bi bi-github text-3xl group-hover:translate-x-1 transition-transform"></i>
            View GitHub Profile
          </Link>
        </div>
      </div>
    </section>
  )
}
